@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Quicksand", sans-serif;
}

/*Flex details*/
.item-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.item-row > * {
  margin: 1rem;
}

p {
  font-size: 15px;
}

/*Colour details*/
.dark-bg {
  background-color: #100f10;
}

.gray-bg {
  background-color: #f6f9fc;
}

.item-white {
  color: #fff;
}

#page-container {
  position: relative;
  min-height: 100vh;
}

#content-wrap {
  padding-bottom: 2.5rem;
  /* Footer height */
}

#footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  height: 2.5rem;
}

.special-icons {
  color: white;
  transition: 300ms linear all;
}
.special-icons:hover {
  color: #5c5ef3;
}

.navbar a {
  color: #212f3b;
  transition: 200ms linear;
}
.navbar a:hover {
  color: #5c5ef3;
}

.img-bg {
  position: relative;
  top: 0;
  left: 0;
}
.img-bg-back {
  position: relative;
  top: 0;
  left: 0;
  filter: blur(3px);
}
.img-bg-front {
  position: absolute;
  top: 10px;
  left: 10px;
}

.text-color-toggle {
  color: #212f3b;
}

.header-lg {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation-name: backgroundColorPalette;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  box-shadow: 3px 20px 5px 0px #ebebeb;
}
.header-sm {
  animation-name: backgroundColorPalette;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  box-shadow: 3px 20px 5px 0px #ebebeb;
}

.btn-d-p {
  background-color: #5c5ef3;
  color: #fff;
  animation-name: backgroundColorPalette;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.btn-d-p-lg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 5em;
}
.btn-d-p:hover {
  background-color: #4547b6 !important;
  color: #fff;
}

.sticky {
  position: -webkit-sticky;
  /* Safari */
  position: sticky;
  top: 0;
  z-index: 1;
}

/*Image details*/
.responsive {
  display: block;
  width: 100%;
  height: auto;
}

/*Bootstrap Navbar*/
.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.8s ease;
  -o-transition: height 0.8s ease;
  transition: height 0.8s ease;
}

.dropdown-menu {
  -webkit-transition: height 0.8s ease;
  -o-transition: height 0.8s ease;
  transition: height 0.8s ease;
}

/*Navbar btn*/
.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgb(0,0,0)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.navbar-toggler:focus {
  outline: 0;
}

.navbar-nav > li {
  padding-left: 1rem;
}

.dropdown-menu .dropdown-item:hover {
  background-color: transparent;
}

.light-hover {
  transition: transform 0.2s;
}
.light-hover:hover {
  transform: scale(1.1);
}

.grayscale {
  filter: grayscale(100%);
  transition: 200ms ease-in-out;
}
.grayscale:hover {
  filter: none;
}

.text-focus-in {
  -webkit-animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

.rotate:hover {
  animation: rotation 7s infinite linear;
}

.big-icon {
  font-size: 5rem;
}

/* Standard syntax */
@keyframes backgroundColorPalette {
  0% {
    background: #5c5ef3;
  }
  25% {
    background: #8a3ffc;
  }
  50% {
    background: #3032aa;
  }
  75% {
    background: #6508bd;
  }
  100% {
    background: #0d48b8;
  }
}
@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}
@keyframes rotation {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}